.product-card{
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-between;
	text-align: center;
	padding: 10px;
	border: 1px solid transparent;
	&:hover{
		border: 1px solid grey;
	}
	&-lbl{
		display: block;
		background-color: $c-main;
		color: $c-inverse;
		padding: 0 10px;
		margin-right: 10px;
		margin-bottom: 10px;
		position: relative;
		text-transform: uppercase;
		font-size: 10px;
		&-blk{
			display: flex;
			flex-wrap: wrap;
		}
		i{
			padding-right: 10px;
		}
	}
	&-b{
		width: 100%;
		&-txt{
			font-size: 14px;
			line-height: 1.3;
			padding-bottom: 5px;
		}
	}
	&-btn{
		@extend .product-btn;
		margin-bottom: 0;
		&:before{
			font-family: FontAwesome;
			content: "\f005";
			display: block;
			position: absolute;
			left: 15px;
		}
		&-inverse{
			@extend .btn-inverse;
			&:before{
				font-family: FontAwesome;
				content: "\f07a";
				display: block;
				position: absolute;
				left: 27px;
			}
		}
		&-wrap{
			margin-bottom: 10px;
		}
	}
}